<template>
  <div>
    <v-row>
      <v-col :class="`splitter_volver_${theme}`">
        <hr
          v-if="!tiny"
          class="hr1"
        >
        <hr
          v-else
          class="hr2"
        >
        <div>
          <a @click="$router.go(-1)">
            {{ text }} <v-icon>mdi-chevron-right</v-icon>
          </a>
        </div>
      </v-col>
    </v-row>
  </div>
</template>
<script>
  export default {
    name: 'SplitterVolver',
    props: {
      text: {
        type: String,
        default: 'Volver',
      },
      tiny: {
        type: Boolean,
        default: null,
      },
      theme: {
        type: String,
        default: 'green',
      },
    },
  }
</script>

<style lang="sass" scoped>

@import '@/sass/variables2'

@mixin splitter_volver($color, $colorHover, $background)
  display: flex
  flex-direction: column
  font-size: 18px
  .hr1, .hr2
    margin: 20px 0px 20px 0px
    height: 3px
    background: $background
  .hr2
    width: 200px
  div
    display: flex
    justify-content: flex-end
    a
      color: $color !important
      text-decoration: none
      font-weight: 500
      .v-icon
        margin-bottom: 1px
        color: $color
    a:hover
      color: $colorHover !important

.splitter_volver_green
  @include splitter_volver($green, $orange, $green)

.splitter_volver_orange
  @include splitter_volver($orange, $green, $orange)

</style>
